.app {
  max-width: 460px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

.app ul {
  padding-left: 50px;
  padding-right: 50px;
}
.app li {
  list-style-type: square;
  border-radius: 50px;
  font-size: 18px;
}

.app p {
  font-size: 18px;
}

.app h1 {
  margin-bottom: 10px;
  padding-bottom: 10px;
  text-align: center;
}

.app h2 {
  margin-top: 8px;
  font-weight: 300;
  font-style: italic;
  color: #595959;
}

.todolist {
  margin-top: 30px;
  border: 2px solid #f2f4f5;
  padding-top: 35px;
}

.todolist.notodos {
  padding-bottom: 15px;
}

.todolist h2 {
  font-weight: 400;
  margin-bottom: 40px;
  font-style: normal;
  color: black;
  text-align: center;
  text-decoration: underline;
}

.todolist p {
  padding-left: 30px;
  font-style: italic;
  font-weight: 300;
  font-size: 16px;
}

button, input {
  font-family: inherit;
  font-size: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
input {
  width: 250px;
  height: 36px;
  border: 2px solid #9ba3aa;
  font-size: 18px;
}
button {
  margin-left: 10px;
  height: 36px;
  border: 2px solid #73787c;
  background-color: #9ba3aa;
  color: white;
  border-radius: 4px;
  font-size: 20px;
}


.todo-input {
  margin-top: 30px;
  margin-bottom: 48px;
}

.footer {
  font-weight: 300;
  text-align: center;
}

a {
  text-decoration: none;
}
a:link {
  color: #707579;
}
a:visited {
  color: #676a6d;
}
a:hover {
  text-decoration: underline;
}
a:active {
  color: #666161;
}
a.active-link {
  color: rgb(114, 104, 211);
  font-weight: lighter;
}

.todolist-list {
  margin-bottom: 35px;
}


@media (max-width: 410px) {
  input {
    width: 100%;
  }
  button {
    margin-left: 0;
    margin-top: 15px;
  }
  .app ul {
    padding-left: 30px;
    padding-right: 15px;
  }
}



